.tokens {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.tokens i {
    display: block;
}
.tokens .secondaries {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.tokens .secondaries i {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28mm;
    height: 28mm;
}
.tokens .secondaries i input {
    text-align: center;
    border: 0;
    width: 20mm;
    font-size: 32pt;
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
}
.tokens .secondaries i.double {
    display: flex;
    flex-direction: column;
}
.tokens .secondaries i.double input {
    font-size: 15pt;
}
.tokens .wounds i {
    width: 25mm;
    height: 25mm;
    background-repeat: no-repeat;
    background-size: 25mm 25mm;
    margin-top: -8mm;
}
.tokens .pace {
    background-image: url(pace.svg);
}
.tokens .parry {
    background-image: url(parry.svg);
}
.tokens .toughness {
    background-image: url(toughnes.svg);
}
.tokens .fatigue-1 {
    background-image: url(fatigue-1.svg);
    margin-top: 0 !important;
}
.tokens .fatigue-2 {
    background-image: url(fatigue-2.svg);
    margin-left: 7.4mm;
}
.tokens .out {
    background-image: url(eliminated.svg);
    margin-left: 1.5mm;
    margin-top: -3.9mm;
}
.tokens .wound-3 {
    background-image: url(wound-3.svg);
    margin-left: 5.1mm;
    margin-top: -3.9mm;
}
.tokens .wound-2 {
    background-image: url(wound-2.svg);
    margin-left: 0.4mm;
    margin-top: -3.9mm;
}
.tokens .wound-1 {
    background-image: url(wound-1.svg);
    margin-left: 5mm;
    margin-top: -3.9mm;
}